body {
  margin: 0;
  padding: 0;
  font-family: Ubuntu;
}
.center{
	text-align: center;
}
.mTop10{
	margin-top: 10px;
}
.mLeft10{
	margin-left: 10px;
}
.bold{
	font-weight: bold;
}
.fSize20{
	font-size: 20px;
}


.container-fluid{
	padding: 0;
}
.input-group input.form-control.input-story{
    -webkit-box-shadow: none;
            box-shadow: none;
    background: #fff;
    border: 1px solid #0D87E9;
    height: 35px;
    padding-left: 10px;
}
.logo{
	font-size: 40px;
    padding: 10px 10px 50px;
}
.roomTitle{
	font-size: 35px;
    background: #fff;
    -webkit-box-shadow: 0px 1px 3px 1px rgba(0,0,0,0.1);
            box-shadow: 0px 1px 3px 1px rgba(0,0,0,0.1);
    margin-bottom: 10px;
    padding: 0 15px;
    position: relative;
    z-index: 2;
}

.roomId{
	font-size: 16px;
}
.userCard{
	width: 155px;
	display: inline-block;
	margin-bottom: 15px;
}
.userCard .user{
	margin-top: -10px;
    font-size: 16px;
    font-weight: bold;
    border-top: 1px dashed #ddd;
    margin-left: 10px;
    border-bottom: 1px dashed #ddd;
    margin-right: 10px;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    overflow: hidden;
}
.noUser{
    font-size: 24px;
    margin-top: 40px;
}
label.teamLabel{
    font-size: 20px;
    margin-top: 5px;
    display: block;
}
.card{
	cursor: pointer;
}
.deck .card.cardSelected{
	-webkit-box-shadow: 0 0px 12px 3px #4CAF50;
	        box-shadow: 0 0px 12px 3px #4CAF50;
	top:-80px;
}
.story{
	font-size: 20px;
}
.storySection{
	margin-bottom: 20px;
}
.members{
	font-size: 16px;
	padding: 5px;
	border-bottom: 1px solid #ccc;
}
.members .glyphicon-ok{
	line-height: 24px;
	margin-right: 5px;
	color: #4CAF50;
}




.deck {
    height: 8em;
    position: fixed;
    right: -24px;
    width: 481px;
    bottom: 125px;
    -webkit-transform: rotate(-36deg);
        -ms-transform: rotate(-36deg);
            transform: rotate(-36deg);
}
.deck .card--heart:before{
	font-size: 35px;
	font-weight: bold;
}
.deck .card:before {
    top: 0px;
    right: 2px;
    opacity: 1;
}
.deck .card{
    -webkit-box-shadow: 0 2px 12px 6px rgba(0, 0, 0, 0.3);
            box-shadow: 0 2px 12px 6px rgba(0, 0, 0, 0.3);
    -webkit-transition: top 0.35s;
    -o-transition: top 0.35s;
    transition: top 0.35s;
    top:0;
}
