.deck div.card-cont:nth-child(1)  { left: 0em; z-index:15}.deck div.card-cont:nth-child(2)  { left: 3em; z-index:14}.deck div.card-cont:nth-child(3)  { left: 6em; z-index:13}.deck div.card-cont:nth-child(4)  { left: 9em; z-index:12}.deck div.card-cont:nth-child(5)  { left: 12em; z-index:11}.deck div.card-cont:nth-child(6)  { left: 15em; z-index:10}.deck div.card-cont:nth-child(7)  { left: 18em; z-index:9}.deck div.card-cont:nth-child(8)  { left: 21em; z-index:8}.deck div.card-cont:nth-child(9)  { left: 24em; z-index:7}.deck div.card-cont:nth-child(10)  { left: 27em; z-index:6}.deck div.card-cont:nth-child(11)  { left: 30em; z-index:5}.deck div.card-cont:nth-child(12)  { left: 33em; z-index:4}.deck div.card-cont:nth-child(13)  { left: 36em; z-index:3}.deck div.card-cont:nth-child(14)  { left: 39em; z-index:2}

/* rotate cards if rotateHand option is on */
.deck.rotateHand div.card-cont:nth-child(1) {
    -webkit-transform: translate(1.9em, .9em) rotate(-42deg);
    -ms-transform: translate(1.9em, .9em) rotate(-42deg);
        transform: translate(1.9em, .9em) rotate(-42deg);
}
.deck.rotateHand div.card-cont:nth-child(2) {
    -webkit-transform: translate(1.5em, .5em) rotate(-33deg);
    -ms-transform: translate(1.5em, .5em) rotate(-33deg);
        transform: translate(1.5em, .5em) rotate(-33deg);
}
.deck.rotateHand div.card-cont:nth-child(3) {
    -webkit-transform: translate(1.1em, .3em) rotate(-24deg);
    -ms-transform: translate(1.1em, .3em) rotate(-24deg);
        transform: translate(1.1em, .3em) rotate(-24deg);
}
.deck.rotateHand div.card-cont:nth-child(4) {
    -webkit-transform: translate(.7em, .2em) rotate(-15deg);
    -ms-transform: translate(.7em, .2em) rotate(-15deg);
        transform: translate(.7em, .2em) rotate(-15deg);
}
.deck.rotateHand div.card-cont:nth-child(5) {
    -webkit-transform: translate(.3em, .1em) rotate(-6deg);
    -ms-transform: translate(.3em, .1em) rotate(-6deg);
        transform: translate(.3em, .1em) rotate(-6deg);
}
.deck.rotateHand div.card-cont:nth-child(6) {
    -webkit-transform: translate(-.1em, .1em) rotate(3deg);
    -ms-transform: translate(-.1em, .1em) rotate(3deg);
        transform: translate(-.1em, .1em) rotate(3deg);
}
.deck.rotateHand div.card-cont:nth-child(7) {
    -webkit-transform: translate(-.5em, .2em) rotate(12deg);
    -ms-transform: translate(-.5em, .2em) rotate(12deg);
        transform: translate(-.5em, .2em) rotate(12deg);
}
.deck.rotateHand div.card-cont:nth-child(8) {
    -webkit-transform: translate(-.9em, .3em) rotate(21deg);
    -ms-transform: translate(-.9em, .3em) rotate(21deg);
        transform: translate(-.9em, .3em) rotate(21deg);
}
.deck.rotateHand div.card-cont:nth-child(9) {
    -webkit-transform: translate(-1.3em, .6em) rotate(30deg);
    /*transform: translate(-1.3em, .6em) rotate(30deg);*/
    -ms-transform: translate(-1.3em, .6em) rotate(36deg);
        transform: translate(-1.3em, .6em) rotate(36deg);
}
.deck.rotateHand div.card-cont:nth-child(10) {
    -webkit-transform: translate(-1.7em, 1em) rotate(39deg);
    -ms-transform: translate(-1.7em, 1em) rotate(39deg);
        transform: translate(-1.7em, 1em) rotate(39deg);
}
.deck.rotateHand div.card-cont:nth-child(11) {
    -webkit-transform: translate(-2.2em, 1.5em) rotate(48deg);
    -ms-transform: translate(-2.2em, 1.5em) rotate(48deg);
        transform: translate(-2.2em, 1.5em) rotate(48deg);
}
.deck.rotateHand div.card-cont:nth-child(12) {
    -webkit-transform: translate(-2.8em, 2.1em) rotate(57deg);
    -ms-transform: translate(-2.8em, 2.1em) rotate(57deg);
        transform: translate(-2.8em, 2.1em) rotate(57deg);
}
.deck.rotateHand div.card-cont:nth-child(13) {
    -webkit-transform: translate(-3.5em, 2.8em) rotate(66deg);
    -ms-transform: translate(-3.5em, 2.8em) rotate(66deg);
        transform: translate(-3.5em, 2.8em) rotate(66deg);
}

.deck.rotateHand div.card-cont {
    position: absolute;
}


